.App-link {
  font-family:Georgia;
  color: deeppink;
    h1{
        font-size: 95px; 
    }
    h2{
        font-size: 50px; 
    }
    padding-left: 50px;
    line-height: 1;
    p{
      padding-top: 50px;
      padding-right: 50px;
      padding-left: 50px;
      color: rgb(0, 0, 0);
      font-size: 30px;
      text-align: middle;
     }
}

.App-hyperlink{
  font-family:Georgia;
  color: black;
    font-size:80px;
    text-decoration: none;
    
    
    &:hover {
        color: rgb(248, 165, 209);
    }
  }

  .container {
    position: relative;
  }

  .mainlink{
  position: absolute;
  font-family:Georgia;
  color: black;
    font-size:50px;
    text-decoration: none;
    top: 73.5%;
    left: 39.5%;
    transform: translate(-50%, -50%);

  &:hover {
    color: rgb(116, 253, 255);
}
}

  .x{
    font-family:Georgia;
    font-size:15px;
    color: deeppink;
  }

  .s{
    font-family:Georgia;
    font-size:35px;
    text-align: center;
    color: rgb(0, 0, 0);
  }

  .center {
    font-family:Georgia;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: deeppink;
  }


